.dialog-nav-container {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 16px 25px;
  background: linear-gradient(
    180deg,
    var(--background) 0%,
    rgba(var(--background-rgb), 0.2) 85%,
    transparent
  );
  z-index: 10;
}

.dialog-nav-back {
  width: 40px;
  cursor: pointer;
}

.dialog-nav-back i {
  font-size: 18px;
}

.dialog-nav-title {
  font-size: 17px;
  font-weight: 600;
}

.dialog-nav-menu-btn {
  width: 40px;
  height: 18px;
  padding: 0 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}

.dialog-nav-menu-dash {
  width: 100%;
  height: 2px;
  background-color: white;
  border-radius: 100px;
}

.dialog-nav-menu-btn.dialog-nav-hidden {
  pointer-events: none;
  opacity: 0;
}
